@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,600;1,400&display=swap);
*{box-sizing:border-box}body{margin:0;font-size:16px;font-display:block;font-family:Montserrat,sans-serif}.content{margin:0 auto;padding:0 10px;max-width:320px}header{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}header h1{font-size:24px;font-weight:600}header button{border:none;outline:none;background:none;cursor:pointer}input{width:100%;height:40px;padding:8px;margin-bottom:10px;border:2px solid #bbb;border-radius:4px;outline:none}input:focus{border:3px solid #aaa}.times{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.time{margin:8px 0;padding:20px;font-weight:600;border-radius:8px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;background-color:#2e8b57;color:#fff}
